import React, { useState } from "react";
import { FaDownload } from "@react-icons/all-files/fa/FaDownload";
import { CSSTransition } from "react-transition-group";

export default function Download() {
	return (
		<article className="page">
			<Info />
			<List />
		</article>
	);
}

const Info = () => {
	return (
		<section>
			<h2 class="unselectable">mAchina downloads</h2>
			<p>Download MACHINA using the buttons below.</p>
			<p>
				<i>
					NOTE: MACHINA is currently in Alpha. Not all features are currently
					available, and not all may be fully functional. Full releases will be available on CurseForge.
				</i>
			</p>
		</section>
	);
};

const List = () => {
	const [activeMenu, setActiveMenu] = useState("0.1.2");
	const DownloadButton = ({ name, version, modloader }) => {
		return (
			<a
				className="link"
				onClick={() => setActiveMenu(activeMenu === version ? "" : version)}
			>
				<div className="link-title">
					<FaDownload />
					<h3 className="nobefore" id="unselectable">
						{name} {version}
					</h3>
				</div>
				<div className="link-description" id="unselectable">
					<span>{modloader}</span>
				</div>
			</a>
		);
	};

	const DownloadInfo = ({ modloader, url, changelog }) => {
		var filename = url.split("/").pop().split("#")[0].split("?")[0];
		return (
			<section>
				<h2 class="unselectable">changelog</h2>
				{changelog}
				<h2 class="unselectable">download</h2>
				<p>
					<a href={url} target="_blank" rel="noopener noreferrer">
						Download {filename} for {modloader}.
					</a>
				</p>
			</section>
		);
	};

	return (
		<div>
			<DownloadButton name="alpha" version="0.1.0" modloader="Forge 1.16" />
			<CSSTransition
				in={activeMenu === "0.1.0"}
				timeout={300}
				classNames="down"
				unmountOnExit
			>
				<DownloadInfo
					modloader="Forge 1.16"
					url="https://download1339.mediafire.com/b2jatmr6tblg/k4qgsnojgsxoehm/machina-1.16.5-alpha-0.1.0.jar"
					changelog={
						<ul>
							<li>First major release of MACHINA!</li>
							<li>
								This version contains all of the content for the overworld.
							</li>
							<li>
								All planets have been disabled to prioritize testing of
								overworld content.
							</li>
							<li>
								Please report any issues directly to the{" "}
								<a
									href="https://github.com/Cy4Shot/MACHINA/issues"
									target="_blank"
									rel="noopener noreferrer"
								>
									Issue Tracker
								</a>
								.
							</li>
						</ul>
					}
				/>
			</CSSTransition>
		</div>
	);
};
